import ImageUpload from '../../components/image-upload.vue';
import PageButton from '../../components/button.vue';

export default {
	components: {
		ImageUpload,
		PageButton
	},
	data() {
		return {
			temp: {
				doctorName: '',
				mobile: '',
				idCard: '',
				avatarUrl: '',
				educationUrl: '',
				title: '',
				educationLevel:'',
				credentialUrl: '',
				introduction: '',
				states:'',
				auditRemark:''
			},
			array: [ '中专','大专', '本科', '硕士','博士'],
			index: null
		}
	},
	onLoad() {
		this.temp = uni.getStorageSync("doctorInfo");
		for (let i = 0; i < this.array.length; i++) {
			if(this.array[i] == this.temp.educationLevel){
				this.index = i;
				break;
			}
		}
	},
	methods: {
		bindPickerChange: function(e) {
			this.index = e.target.value
			this.temp.educationLevel = this.array[this.index]
		},
		getAvatarUrl:function(v){
			this.temp.avatarUrl=v;
		},
		getCredentialUrl:function(v){
			this.temp.credentialUrl=v;
		},
		getEducationUrl:function(v){
			this.temp.educationUrl=v;
		},
		submit:function(){
			let that = this;
			if(!that.$util.isEmpty(that.temp.doctorName)){
				uni.showToast({
					title: '请输入真实姓名',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.mobile)){
				uni.showToast({
					title: '请输入手机号',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.IdentityCodeValid(that.temp.idCard)){
				uni.showToast({
					title: '请输入正确的身份证号',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.avatarUrl)){
				uni.showToast({
					title: '请上传头像',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.educationLevel)){
				uni.showToast({
					title: '请选择教育程度',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.educationUrl)){
				uni.showToast({
					title: '请上传学历证书',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.title)){
				uni.showToast({
					title: '请输入医院职称',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.credentialUrl)){
				uni.showToast({
					title: '请上传职业资格证书',
					icon:'none',
					duration: 2000
				});
				return;
			}
			if(!that.$util.isEmpty(that.temp.introduction)){
				uni.showToast({
					title: '请输入简介',
					icon:'none',
					duration: 2000
				});
				return;
			}
			that.$util.request({
				url: "/didi-doctor/doctorinfo/updateInfo",
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.setStorageSync('doctorInfo', res.data);
					uni.showToast({
						title: '提交成功',
						icon: 'success',
						duration: 2000
					})
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 500)
				},
				error: function() {}
			})
		}
	}
}
